import React from "react";
import { api } from "../App";
import Alert from "./Alert";
import { UserContext } from "../App";

export default function AdminTable(props) {

    const { model, setModel, models, setModels, sort, defaultModel } = props;

    const userContext = React.useContext(UserContext);
    const [error, setError] = React.useState(null);
    const [success, setSuccess] = React.useState(null);

    const CREATE = "Create",
        EDIT = "Edit";
    const [state, setState] = React.useState(CREATE); // Create, Edit

    React.useEffect(() => {
        api.get(props.apiPath).then(res => {
            let data = res.data;
            sort(data);
            setModels(data);
        }).catch(err => {
            console.log(err);
        });
    }, []);

    function reset(e) {
        e.preventDefault();
        setError(null);
        setSuccess(null);
        // setState(CREATE);
        setModel(defaultModel);
        document.querySelector("form[id=mainForm]").reset();
        document.querySelectorAll("button").forEach(b => b.disabled = false);
        props.reset && props.reset(e);
    }

    function handleDelete(e) {
        reset(e);
        const id = e.target.value;
        api.delete(`${props.apiPath}/${id}`).then(res => {
            if (res.status.toString().startsWith("2")) {
                setModels(models.filter(m => m.id != id));
                setSuccess("Item deleted successfully");
            }
            else {
                setError(JSON.stringify(res));
            }
        }).catch(err => {
            setError(JSON.stringify(err.message));
        });
        props.handleDelete && props.handleDelete(e);
    }

    function handleEdit(e) {
        reset(e);
        e.target.disabled = true;
        setState(EDIT);
        const id = e.target.value;
        const model = models.find(s => s.id == id);
        setModel(model);
    }

    function handleCreate(e) {
        reset(e);
        setState(CREATE);
        props.handleCreate && props.handleCreate(e);
    }

    function submit(e) {
        document.querySelector("button[id=submit]").disabled = true;
        api.post(`${props.apiPath}/upsert`, model)
            .then(res => {
                if (res.status.toString().startsWith("2")) {
                    setModel(res.data);
                    let data;
                    if (state === CREATE) {
                        data = [...models, res.data];
                    } else {
                        data = models.map(s => s.id == res.data.id ? res.data : s);
                    }
                    sort(data);
                    setModels(data);
                    reset(e);
                    setSuccess("model saved successfully");
                }
                else {
                    setError(JSON.stringify(res));
                }
            })
            .catch(err => {
                setError(JSON.stringify(err.message));
            })
            .finally(() => {
                document.querySelector("button[id=submit]").disabled = false;
            });
    }

    return (<div className="w-full max-w-md sm:max-w-none h-full tajawal lg:mt-5">
        <form id="mainForm">
            <h1 className="flex justify-between">
                <span className="text-2xl">
                    {state} {model ? model.title : props.title}
                </span>
                <button className="btn btn-primary" onClick={handleCreate}>
                    New
                </button>
            </h1>
            {props.children}
            <div className="mt-5">
                <button id="submit" className="btn btn-primary btn-block" onClick={submit}>{state}</button>
                {error && <Alert className="alert-error">
                    {error}
                </Alert>}
                {success && <Alert className="alert-success">
                    {success}
                </Alert>}
            </div>
        </form>
        <div className="mt-10">

        </div>
        <h1 className="text-2xl">Your {props.title}</h1>
        <div className="overflow-auto w-full max-h-[30rem]">
            <table className="table">
                {/* head */}
                <thead className="sticky top-0 bg-base-100">
                    <tr>
                        {props.cols?.map((col, i) => (
                            <th key={i}>{col.label}</th>
                        ))}
                        {userContext.isAdmin && <th>Actions</th>}
                    </tr>
                </thead>
                <tbody>
                    {
                        models?.map((model, i) => (
                            <tr key={i}>
                                {props.cols?.map((col, j) => (
                                    <td key={j}>{col.getValue(model)}</td>
                                ))}
                                {userContext.isAdmin && <td>
                                    <button className="btn btn-sm btn-error hover:bg-red-900" value={model.id} onClick={e => {
                                        return window.confirm("Are you sure you want to delete this item?") ? handleDelete(e) : null;
                                    }}>Delete</button>
                                    <button className="btn btn-sm btn-secondary" value={model.id} onClick={handleEdit}>Edit</button>
                                </td>}
                            </tr>
                        ))
                    }
                </tbody>
            </table>
        </div>
        <br />
        <br />
    </div>);
}